import BaseElement from '../base-element/base-element';
export const div = (params, ...children) => new BaseElement(Object.assign({ tag: 'div' }, params), ...children);
export const span = (params, ...children) => new BaseElement(Object.assign({ tag: 'span' }, params), ...children);
export const header = (params, ...children) => new BaseElement(Object.assign({ tag: 'header' }, params), ...children);
export const main = (params, ...children) => new BaseElement(Object.assign({ tag: 'main' }, params), ...children);
export const footer = (params, ...children) => new BaseElement(Object.assign({ tag: 'footer' }, params), ...children);
export const h1 = (params, ...children) => new BaseElement(Object.assign({ tag: 'h1', styles: ['h1'] }, params), ...children);
export const h2 = (params, ...children) => new BaseElement(Object.assign({ tag: 'h2', styles: ['h2'] }, params), ...children);
export const h3 = (params, ...children) => new BaseElement(Object.assign({ tag: 'h3', styles: ['h3'] }, params), ...children);
export const h4 = (params, ...children) => new BaseElement(Object.assign({ tag: 'h4', styles: ['h4'] }, params), ...children);
export const p = (params, ...children) => new BaseElement(Object.assign({ tag: 'p', styles: ['p'] }, params), ...children);
export const ul = (params, ...children) => new BaseElement(Object.assign({ tag: 'ul' }, params), ...children);
export const li = (params, ...children) => new BaseElement(Object.assign({ tag: 'li' }, params), ...children);
export const button = (params, ...children) => new BaseElement(Object.assign({ tag: 'button', styles: ['button'] }, params), ...children);
export const input = (params, ...children) => new BaseElement(Object.assign({ tag: 'input', styles: ['input'] }, params), ...children);
export const checkbox = (params, ...children) => new BaseElement(Object.assign({ tag: 'input', styles: ['checkbox'], attributes: { type: 'checkbox' } }, params), ...children);
export const img = (params, ...children) => new BaseElement(Object.assign({ tag: 'img' }, params), ...children);
export const form = (params, ...children) => new BaseElement(Object.assign({ tag: 'form' }, params), ...children);
export const label = (params, ...children) => new BaseElement(Object.assign({ tag: 'label' }, params), ...children);
export const select = (params, ...children) => new BaseElement(Object.assign({ tag: 'select' }, params), ...children);
export const option = (params, ...children) => new BaseElement(Object.assign({ tag: 'option' }, params), ...children);
export const a = (params, ...children) => new BaseElement(Object.assign({ tag: 'a', styles: ['link'] }, params), ...children);
