___CSS_LOADER_EXPORT___.push([module.id, `.loginForm {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  z-index: 1;
  top: 0;
  width: 100%;
}

.loginForm::before {
  content: '';
  display: block;
  position: fixed;
  z-index: -1;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: url(${___CSS_LOADER_URL_REPLACEMENT_0___}) center 80px no-repeat;
  filter: blur(5px);
}

.loginForm::after {
  content: '';
  display: block;
  position: absolute;
  width: 100%;
  height: 240px;
  background: url(${___CSS_LOADER_URL_REPLACEMENT_1___}) no-repeat center;
  bottom: 50px;
  transform: translateY(100%);
  z-index: 1;
}

.loginForm__wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  background-color: rgba(255, 255, 255, 0.6);
}

.loginForm__form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  max-width: 1200px;
  padding: 80px 20px 100px;
  gap: 20px;
}

.loginForm__header {
  font-family: 'Nunito', sans-serif;
  font-size: 36px;
}

.loginForm__btn {
  width: 360px;
  max-width: 100%;
  font-size: 20px;
  background: var(--btn-dark-bg-color);
  color: var(--light-text-color);
  padding: 12px 16px;
  border-radius: 8px;
}

.loginForm__btn:hover {
  background: var(--btn-active-color);
}
