___CSS_LOADER_EXPORT___.push([module.id, `.row {
  display: flex;
  justify-content: space-between;
  width: var(--desktop-width);
  height: 50.5px;
  border-radius: 5px;
  background: var(--light-bg-color);
}

@media (max-width: 980px) {
  .row {
    width: var(--laptop-width);
  }
}

@media (max-width: 860px) {
  .row {
    width: var(--tablet-width);
  }
}

.row__item {
  position: relative;
  flex-grow: 1;
}

.row__item--fill {
  flex-grow: 0;
}

.row--active {
  -webkit-box-shadow: 0px 5px 28px 1px rgba(82, 125, 240, 0.32);
  -moz-box-shadow: 0px 5px 28px 1px rgba(82, 125, 240, 0.32);
  box-shadow: 0px 5px 28px 1px rgba(82, 125, 240, 0.32);
}

.row.hover {
  border: solid 1px var(--btn-active-color);
}

.row--active .row__item--hover {
  border: solid 1px var(--btn-active-color);
}
