___CSS_LOADER_EXPORT___.push([module.id, `.select {
  display: none;
}

.select--active {
  position: fixed;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 20;
  width: 100vw;
  height: 100vh;
  background-color: #303650cf;
  backdrop-filter: blur(2px);
  padding: 0 20px;
}

.select__wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  max-width: 640px;
  height: 640px;
  padding: 50px 10px 50px 20px;
  gap: 35px;
  background-color: var(--dark-bg-color);
  border-radius: 10px;
  -webkit-box-shadow: 0px -1px 20px 9px rgba(4, 10, 26, 0.3);
  -moz-box-shadow: 0px -1px 20px 9px rgba(4, 10, 26, 0.3);
  box-shadow: 0px -1px 20px 9px rgba(4, 10, 26, 0.3);
}

.select__close {
  position: absolute;
  display: flex;
  flex-direction: column;
  width: 40px;
  height: 40px;
  top: 10px;
  right: 5px;
  cursor: pointer;
}

.select__close::before,
.select__close::after {
  content: '';
  width: 20px;
  height: 2px;
  background: #6a7093;
  transform: translate(12px, 16px) rotate(45deg);
  transition: all 0.2s linear;
}

.select__close::after {
  transform: translate(12px, 28px) rotate(-45deg);
}

.select__close:hover:before,
.select__close:hover:after {
  background: #a9afdc;
  transition: all 0.2s linear;
}

.select__title {
  font-family: Nunito, sans-serif;
  font-size: 36px;
  font-weight: 300;
  color: var(--light-text-color);
}
