  display: flex;
  justify-content: space-between;
  width: var(--desktop-width);
}

@media (max-width: 980px) {
  .field__header {
    width: var(--laptop-width);
  }
}

@media (max-width: 860px) {
  .field__header {
    width: var(--tablet-width);
  }
}

