___CSS_LOADER_EXPORT___.push([module.id, `.round {
  display: flex;
  align-items: center;
  border-radius: 25px;
  padding: 22px 10px;
  gap: 15px;
  background: var(--light-bg-color);
  transition: all 0.5s ease;
  cursor: pointer;
  max-width: 580px;
}

.round:hover {
  box-shadow: inset 0 0 0 1px #3f72ee;
  transition: all 0.5s ease;
}

.round:hover .round__number {
  color: #4e6fcd;
}

.round__number {
  text-align: center;
}

.round__words {
  display: flex;
  flex-wrap: wrap;
  align-content: flex-start;
  width: 100%;
  height: 100%;
  gap: 10px;
}

.round__word {
  padding: 4px 10px;
  border: 1px solid var(--btn-active-color);
  border-radius: 60px;
  color: var(--light-text-color);
  font-family: Nunito, sans-serif;
  font-size: 16px;
  font-weight: 300;
}

.round__img {
  width: 116px;
  height: 76px;
  filter: blur(1px);
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
}
